//Countdown codes begin here...
const days_container = document.querySelector('.days .figures')
const hours_container = document.querySelector('.hours .figures')
const mins_container = document.querySelector('.mins .figures')
const secs_container = document.querySelector('.secs .figures')
const interval = 1000;

var days = 124
var hours = 24
var mins = 60
var secs = 60

// mins_container.textContent = mins


const conutdown = setInterval(() => {

  if (mins < 10) {
    mins_container.textContent = '0' + mins

  } else {
    mins_container.textContent = mins
  }

  if (hours < 10) {
    hours_container.textContent = '0' + hours

  } else {
    hours_container.textContent = hours
  }

  if (days < 10) {
    days_container.textContent = '0' + days
  } else {
    days_container.textContent = days
  }


  if (secs > 1) {
    secs--
    secs_container.textContent = secs

  } else {
    secs = 60
    mins--

    if (mins < 0) {
      hours--
      mins = 60
      mins--



      if (hours < 0) {
        hours = 24
        days--
        days_container.textContent = days
      }




    }

    secs_container.textContent = '00'
    mins_container.textContent = mins

    if (hours < 10) {
      hours_container.textContent = '0' + hours

    } else {
      hours_container.textContent = hours
    }

  }




}, interval); 
